import * as React from 'react';
import {
  JsonML,
  getTagName,
  getAttributes,
  getChildren,
  sanitizeUrl,
} from '@ali/4ever-utils';
import type { ConvertFunc } from '@ali/4ever-cangjie';

export default {
  name: 'a',

  match(node: JsonML): boolean {
    return getTagName(node) === 'a';
  },

  convert(node: JsonML, path: number[], convert: ConvertFunc): React.ReactElement {
    // @ts-ignore
    const { href } = getAttributes(node);
    const children = getChildren(node)
      .map((child, index: number) => convert(child, [...path, index]));

    const validUrl = sanitizeUrl(href)

    return (
      <a href={validUrl} data-link-href-cangjie={validUrl} target="_blank" rel="noopener noreferrer" key={path.toString()}>
        {children}
      </a>
    );
  },
};
